import React from "react";
import { MockButton } from "./MockButton";

export const MockDashboard: React.FC = () => {
  return (
    <div
      style={{
        backgroundColor: "white",
        padding: 30,
        borderRadius: 16,
        boxShadow: "0 10px 30px rgba(0,0,0,0.1)",
        width: 800,
        fontFamily: "sans-serif",
      }}
    >
      <h2
        style={{
          borderBottom: "2px solid #eee",
          paddingBottom: 10,
          marginTop: 0,
        }}
      >
        
      </h2>

      <div style={{ display: "flex", gap: 20, marginBottom: 30 }}>
        <div
          style={{
            flex: 1,
            backgroundColor: "#f8f9fa",
            padding: 20,
            borderRadius: 8,
          }}
        >
          <div style={{ fontSize: 14, color: "#666" }}></div>
          <div style={{ fontSize: 32, fontWeight: "bold", color: "#333" }}>
            45,000
          </div>
        </div>
        <div
          style={{
            flex: 1,
            backgroundColor: "#f8f9fa",
            padding: 20,
            borderRadius: 8,
          }}
        >
          <div style={{ fontSize: 14, color: "#666" }}></div>
          <div style={{ fontSize: 32, fontWeight: "bold", color: "#007bff" }}>
            128
          </div>
        </div>
      </div>

      <h3 style={{ fontSize: 18, color: "#555" }}></h3>
      <ul style={{ listStyle: "none", padding: 0 }}>
        {[
          { name: "", count: 42, active: true },
          { name: "", count: 0, active: false },
          { name: "", count: 85, active: true },
        ].map((item, idx) => (
          <li
            key={idx}
            style={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              padding: "15px 0",
              borderBottom: "1px solid #eee",
              opacity: item.active ? 1 : 0.5,
            }}
          >
            <span style={{ fontSize: 20, fontWeight: "bold" }}>
              {item.name}
            </span>
            <div style={{ display: "flex", alignItems: "center", gap: 20 }}>
              <span
                style={{
                  fontSize: 18,
                  color: item.active ? "#28a745" : "#dc3545",
                }}
              >
                {item.active ? ` ${item.count}` : " (SOLD OUT)"}
              </span>
              <MockButton
                text={item.active ? "" : ""}
                color={item.active ? "#dc3545" : "#28a745"}
                style={{ fontSize: 14, padding: "8px 16px" }}
              />
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};
